import { Router } from 'express'
import shoppingcartController from './controllers/shoppingcart/shoppingcartController';



export default(io) => {

    const router = Router()

    io.on("connection", function(socket){
        console.log("Socket connection Establishied with iD: " + socket.id)
        //AGREGAMOS RUTAS
        router.use(shoppingcartController(socket))
    })

    return router

}